<h1 id="android-runtime-configuration-changes" tabindex="-1"><a class="header-anchor" href="#android-runtime-configuration-changes" aria-hidden="true">#</a> Android Runtime Configuration Changes</h1>
<p>So, how can Android so easily support i18n? The secret is through how it handles runtime configuration changes. When the user changes language, all the GUI is destroyed and then re-created. The hard part is to make sure that it's re-created exactly the way the user left it!</p>
