<h2 id="example" tabindex="-1"><a class="header-anchor" href="#example" aria-hidden="true">#</a> Example</h2>
<p><code>MainActivity</code> with a button functioning as a counter starting on 0. It retains its state using the <code>savedInstanceState</code> parameter.</p>
